<template>
  <div class="container">
    
    <h1>Usuarios</h1>
    <b-alert
      :show="dismissCountDown"
      dismissible
      :variant="mensaje.color"
      @dismissed="dismissCountDown = 0"
      @dismiss-count-down="countDownChanged"
    >
      {{ mensaje.texto }}
    </b-alert>

    <form @submit.prevent="editarUsuario(usuarioEditar)" v-if="editar">
      <h3>Editar Usuario</h3>

      <input type="text" class="form-control my-2" placeholder="Nombre" v-model="usuarioEditar.nombre">
      <input type="text" class="form-control my-2" placeholder="Email" v-model="usuarioEditar.email">
      <input type="text" class="form-control my-2" placeholder="Contraseña" v-model="usuarioEditar.pass">
      
      <label>Vista:</label>
      <select class="form-control my-2" v-model="usuarioEditar.vista">
          <option value="tienda">Tienda</option>
      </select>

      <label>Role:</label>
      <select class="form-control my-2" v-model="usuarioEditar.role">
          <option value="USER">Usuario</option>
          <option value="ADMIN">Administrador</option>
      </select>

      <label>Activo:</label>
      <select class="form-control my-2" v-model="usuarioEditar.activo">
          <option value="true">Sí</option>
          <option value="false">No</option>
      </select>


      <button type="submit" class="btn btn-success my-1 mx-2">Editar</button>
      <button type="submit" class="btn btn-secondary my-1" @click="editar = false">Cancelar</button>
    </form>

    <form @submit.prevent="agregarUsuario()" v-if="!editar">
      <h3>Agregar nuevo usuario</h3>
      <input type="text" class="form-control my-2" placeholder="Nombre" v-model="usuario.nombre">
      <input type="text" class="form-control my-2" placeholder="Email" v-model="usuario.email">
      <input type="text" class="form-control my-2" placeholder="Contraseña" v-model="usuario.pass">

      <label>Vista:</label>
      <select class="form-control my-2" v-model="usuario.vista">
          <option value="tienda">Tienda</option>
      </select>

      <label>Role:</label>
      <select class="form-control my-2" v-model="usuario.role">
          <option value="USER">Usuario</option>
          <option value="ADMIN">Administrador</option>
      </select>
      
      <label>Activo:</label>
      <select class="form-control my-2" v-model="usuario.activo">
          <option value="true">Sí</option>
          <option value="false">No</option>
      </select>


      <div class="d-grid gap-2">
        <button type="submit" class="btn btn-success my-2">Agregar</button>
      </div>
    </form>

    <table class="table">
      <thead>
        <tr>
            <th scope="col">Registro</th>
            <th scope="col">Nombre</th>
            <th scope="col">email</th>
            <th scope="col">Vista</th>
            <th scope="col">Role</th>
            <th scope="col">Activo</th>
            <th scope="col">Acciones</th>
        </tr>
        
      </thead>
      <tbody>
        <tr v-for="(item, index) in usuarios" :key="index">
            <td>{{ formatearFecha(item.date) }}</td>
            <td>{{ item.nombre }}</td>
            <td>{{ item.email }}</td>
            <td>{{ item.vista }}</td>
            <td>{{ item.role }}</td>
            <td>{{ item.activo ? "Sí" : "No" }}</td>
          <td>
            <b-button class="btn-danger btn-sm" @click="eliminarUsuario(item._id)">Eliminar</b-button>
            <b-button class="btn-primary btn-sm m-1" @click="activarEdicion(item._id)">Editar</b-button>
          </td>
        </tr>
      </tbody>
    </table>

    <nav aria-label="Page navigation example">
      <ul class="pagination">
        <li class="page-item" :class="{ disabled: paginaActual === 1 }">
          <router-link class="page-link" :to="{ query: { pagina: paginaActual - 1 } }">
            Anterior
          </router-link>
        </li>
        <li class="page-item" :class="{active: paginaActual === index + 1 }" v-for="(item, index) in cantidadPaginas" :key="index">
          <router-link :to="{ query: { pagina: index + 1 } }" class="page-link" >{{ index + 1 }}</router-link>
        </li>
        <li class="page-item" :class="{ disabled: paginaActual === cantidadPaginas }">
          <router-link class="page-link" :to="{ query: { pagina: paginaActual + 1 } }">
            Siguiente
          </router-link>
        </li>
      </ul>
    </nav>
  </div>
</template>

<script>
import {mapState} from 'vuex'
import moment from 'moment'
export default {
    data(){
        return{
            totalUsuarios: 0,
            limite: 50,
            paginaActual: 1,

            usuarios: [],
            dismissSecs: 5,
            dismissCountDown: 0,
            mensaje: { color: "", texto: "" },
            usuario: {nombre: "", email: "", pass: "", vista: "", role: "", activo: true},

            usuarioEditar:{},
            editar: false,
        }
    },

    computed:{
        ...mapState(["token", "usuarioDB"]),
        cantidadPaginas(){
            return Math.ceil(this.totalUsuarios / this.limite)
        }
    },

    methods: {
        alerta() {
        this.mensaje.color = "success";
        this.mensaje.texto = "Probando alerta";
        this.showAlert();
        },
        
        paginacion(pagina){
            let config = {
                headers: {token: this.token}
            };
            let skip = (pagina - 1) * this.limite

            this.axios
                .get(`/usuario?limite=${this.limite}&skip=${skip}`, config)
                .then(res =>{

                  this.usuarios = res.data.usuarioDB;
                  this.totalUsuarios = res.data.totalUsuarios;
                })
                .catch(err => {
                  console.log(err.response);
                })
        },

        agregarUsuario(){
          let config = {
            headers:{token: this.token}
          }
          this.axios
            .post('/nuevo-usuario', this.usuario, config)
            .then((res)=>{
              this.usuarios.push(res.data);
              this.usuario.nombre = "";
              this.usuario.email = "";
              this.usuario.pass = "";
              this.usuario.vista = "";
              this.usuario.activo = true;

              this.mensaje.color = "success";
              this.mensaje.texto = "Usuario agregado";
              this.showAlert();
            })
            .catch((err) => {
              console.log(err.response);
              if(err.response.data.errors.nombre.message){
                this.mensaje.texto = err.response.data.errors.nombre.message;
              }else{
                this.mensaje.texto = "Error de sistema";
              }
              this.mensaje.color = "danger";
              this.showAlert();
            })
        },

        eliminarUsuario(id){
          let config = {
            headers:{token: this.token}
          }
          this.axios
            .delete(`/usuario/${id}`, config)
            .then((res) =>{
              const index = this.usuarios.findIndex(
                (item) => item._id === res.data._id
              );
              this.usuarios.splice(index, 1);
              this.mensaje.color = "success";
              this.mensaje.texto = "Usuario eliminado";
              this.showAlert();
            })
            .catch((err) =>{
              this.mensaje.color = "danger";
              this.mensaje.texto = "Error al eliminar";
              this.showAlert();
            })
        },

        activarEdicion(id){
          this.editar = true
          let config = {
            headers:{token: this.token}
          }
          this.axios.get(`/usuario/${id}`, config)
            .then((res) =>{
              this.usuarioEditar = res.data;
            })
            .catch((err) =>{
              console.log(err);
              this.editar = false;
              this.mensaje.color = "danger";
              this.mensaje.texto = "Error obtener usuario"
            })
        },

        editarUsuario(item){
          let config = {
            headers:{token: this.token}
          }
          this.axios.put(`/usuario/${item._id}`, item, config).then((res) =>{
            const index = this.usuarios.findIndex((n) => n._id === res.data._id);
            this.usuarios[index].nombre = res.data.nombre;
            this.usuarios[index].email = res.data.email;
            this.usuarios[index].pass = res.data.pass;
            this.usuarios[index].vista = res.data.vista;
            this.usuarios[index].role = res.data.role;
            this.usuarios[index].activo = res.data.activo;

            this.mensaje.color = "success";
            this.mensaje.texto = "Usuario Actualizado";
            this.showAlert();
            this.editar = false;
          })
            .catch((err) => {
              console.log(err.response);
              this.mensaje.color = "danger";
              this.mensaje.texto = "No fue posible editar la informacion del usuario";
              this.showAlert();
            })
        },

        countDownChanged(dismissCountDown){
          this.dismissCountDown = dismissCountDown;
        },
        showAlert(){
          this.dismissCountDown = this.dismissSecs;
        },
        formatearFecha(fecha){
          moment.locale('es-mx');
          return moment(fecha).format('L');
        }

    },

    watch:{
      "$route.query.pagina":{
        immediate: true,
        handler(pagina){
          pagina = parseInt(pagina) || 1;
          this.paginacion(pagina);
          this.paginaActual = pagina;
        }
      }
    }

}
</script>

